/* Submenu Styles
-------------------------------------------------------------- */
#adminmenu a[href^="edit.php?post_type=download"]:hover, #adminmenu a[href^="edit.php?post_type=download"]:focus {
  box-shadow: inset 4px 0 0 0 currentColor;
  transition: box-shadow .1s linear; }

/* Secondary Separators */
#adminmenu ul > li:not(:last-child) a[href^="post-new.php?post_type=download"]:after,
#adminmenu ul > li:not(:last-child) a[href^="edit.php?post_type=download&page=cs-discount"]:after,
#adminmenu ul > li:not(:last-child) a[href^="edit.php?post_type=download&page=cs-reports"]:after,
#adminmenu #menu-posts-download ul > li:nth-last-child(2):after {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  display: block;
  float: left;
  margin: 13px -15px 8px;
  content: '';
  width: calc(100% + 30px); }
  @media screen and (max-width: 782px) {
    #adminmenu ul > li:not(:last-child) a[href^="post-new.php?post_type=download"]:after,
    #adminmenu ul > li:not(:last-child) a[href^="edit.php?post_type=download&page=cs-discount"]:after,
    #adminmenu ul > li:not(:last-child) a[href^="edit.php?post_type=download&page=cs-reports"]:after,
    #adminmenu #menu-posts-download ul > li:nth-last-child(2):after {
      margin: 20px -20px 8px -20px;
      width: calc(100% + 40px); } }

#adminmenu #menu-posts-download {
  /* WordPress 5.7 fix for left-shadow on hover */ }
  #adminmenu #menu-posts-download ul > li:nth-last-child(2):after {
    margin-left: 0; }
  #adminmenu #menu-posts-download ul.wp-submenu-wrap li {
    clear: both; }
  #adminmenu #menu-posts-download a.wp-has-current-submenu:after {
    display: none; }

/* Show Submenu Arrow */
ul#adminmenu #menu-posts-download ul.wp-submenu li.current a:before {
  right: 0;
  border: solid 8px transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-right-color: #f1f1f1;
  margin-top: 2px; }
